<template>
	<view class="resource">
		
	</view>
</template>

<script setup>
 import {ref,reactive,onMounted} from 'vue';
 import {getResourceList} from '../../common';
 const state = reactive({
	 pageNumber:1,
	 pageSize:50,
	 selectType:''
 });
 const list = ref([]);
 onMounted(async() => {
	
	 const res = await getArticleList(state);
	 list.value = res.list;
 })
</script>

